"use client";
import React, { useEffect, useState } from "react";
import Link from "next/link";
import ChatBox from "./components/ChatBox";
import ProfileModal from "./components/ProfileModal";

export default function Home() {
  const [scale, setScale] = useState(1);
  const [offsetX, setOffsetX] = useState(0);
  const [offsetY, setOffsetY] = useState(0);
  const [showLogin, setShowLogin] = useState(false); // 一開始不顯示登入視窗
  const [loggedIn, setLoggedIn] = useState(false);   // 一開始不顯示頭像
  const [formData, setFormData] = useState({
    department: "",
    name: "",
    nickname: "",
    avatar: "/avatar1.png",
  });
  const [showProfile, setShowProfile] = useState(false);
  const [profileUser, setProfileUser] = useState<{ nickname?: string; avatar?: string; profession?: string; loginDays?: number } | undefined>(undefined);

  // 新增：全域登入使用者狀態
  const [currentUser, setCurrentUser] = useState<{
    nickname: string;
    avatar: string;
    loggedIn: boolean;
  }>( {
    nickname: "",
    avatar: "",
    loggedIn: false,
  });

  // 職業列表
  const professions = [
    "薪水小偷", "動物管理員", "總統", "垃圾車", "無敵星星", "神射手", "老闆",
    "愛情顧問", "低調宅宅", "瞌睡蟲", "哭哭", "香菇", "貓咪", "狗狗", "社畜",
    "主管", "法師", "獵人", "戰士", "盜賊", "牧師", "聖騎士", "德魯伊", "薩滿"
  ];
  const [jobIndex, setJobIndex] = useState(0);
  const [loginDays, setLoginDays] = useState(1);

  useEffect(() => {
    const updateScale = () => {
      const baseWidth = 1280;
      const baseHeight = 720;
      const scaleX = window.innerWidth / baseWidth;
      const scaleY = window.innerHeight / baseHeight;
      const scale = Math.max(scaleX, scaleY);
      setScale(scale);
      setOffsetX((window.innerWidth - baseWidth * scale) / 2);
      setOffsetY((window.innerHeight - baseHeight * scale) / 2);
    };
    updateScale();
    window.addEventListener("resize", updateScale);
    return () => window.removeEventListener("resize", updateScale);
  }, []);

  const handlePrevJob = () => {
    setJobIndex((prev) => (prev - 1 + professions.length) % professions.length);
  };

  const handleNextJob = () => {
    setJobIndex((prev) => (prev + 1) % professions.length);
  };

  const handleLogin = async () => {
    try {
      const res = await fetch("/api/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          ...formData,
          profession: professions[jobIndex],
          loginDays,
        }),
      });

      const data = await res.json();
      console.log("後端回應：", data);

      if (data.success) {
        setLoggedIn(true);
        setShowLogin(false);

        // 新增：登入成功同步 currentUser（可按需調整 avatar 路徑或 nickname）
        setCurrentUser({
          nickname: formData.nickname || "訪客",
          avatar: "/game_04.png",
          loggedIn: true,
        });
      }
    } catch (err) {
      console.error("登入失敗", err);
    }
  };

  return (
    <main
      style={{
        width: "100vw",
        height: "100vh",
        backgroundImage: "url('/game_00.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        position: "relative",
      }}
    >
      {/* 可模糊的整個舞台區塊，showProfile 時套用 filter */}
      <div style={{ width: "100%", height: "100%", filter: showProfile ? "blur(6px) saturate(0.9)" : "none", transition: "filter 200ms ease" }}>
        {/* 舞台 (1280x720) */}
        <div
          style={{
            width: "1280px",
            height: "720px",
            position: "absolute",
            top: `${offsetY}px`,
            left: `${offsetX}px`,
            transform: `scale(${scale})`,
            transformOrigin: "top left",
          }}
        >
          <img src="/game_00.png" alt="背景" style={{ position: "absolute", width: "1280px", height: "720px", top: 0, left: 0 }} />

          {/* 公告欄（你原本的） */}
          <div style={{ position: "absolute", bottom: "110px", left: "360px", width: "560px", zIndex: 999 }}>
            {/* ...公告內容... */}
          </div>

          {/* ChatBox：inline 模式（跟背景一起縮放），放在公告欄左側（或改 right/bottom） */}
          <ChatBox
            currentUser={currentUser}
            usePortal={false}
            left="50px"
            bottom="198px"
            width={280}
            height={148}
            bubbleMinHeight={32}
            bubbleVerticalPadding={6}
          />

          {/* 放在 stage 內：Log out 與使用者顯示會跟背景一起 scale */}
          {loggedIn && (
            <>
              <button
                onClick={() => {
                  // 登出處理（維持原有行為）
                  setLoggedIn(false);
                  setShowProfile(false);
                  setProfileUser(undefined);
                  setCurrentUser({ nickname: "", avatar: "", loggedIn: false });
                  setFormData({ department: "", name: "", nickname: "", avatar: "/avatar1.png" });
                  setShowLogin(false);
                }}
                style={{
                  position: "absolute",
                  top: 20,    // 以 stage 左上為基準的 px（會跟著 scale）
                  right: 40,
                  background: "rgba(0,0,0,0.6)",
                  color: "white",
                  padding: "6px 10px",
                  borderRadius: 6,
                  border: "1px solid rgba(255,255,255,0.12)",
                  cursor: "pointer",
                  fontWeight: 700,
                  fontSize: 14,
                  zIndex: 9999,
                }}
              >
                Log out
              </button>

              <div
                style={{
                  position: "absolute",
                  top: 64,      // 以 stage 為座標系，會跟背景放大縮小
                  right: 40,
                  width: 125,
                  maxHeight: 160,
                  overflowY: "auto",
                  padding: "6px 8px",
                  background: "rgba(0,0,0,0.45)",
                  borderRadius: 8,
                  display: "flex",
                  flexDirection: "column",
                  gap: 8,
                  zIndex: 9999,
                  boxSizing: "border-box",
                  pointerEvents: "auto",
                }}
              >
                {[currentUser]
                  .filter((u) => !!u && (u.loggedIn ?? loggedIn))
                  .map((u, i) => (
                    <div key={i} style={{ display: "flex", alignItems: "center", gap: 8 }}>
                      <div style={{ width: 28, height: 28, borderRadius: "50%", overflow: "hidden", background: "#333", flexShrink: 0 }}>
                        <img src={u?.avatar || formData.avatar} alt="avatar" style={{ width: "100%", height: "100%", objectFit: "cover", display: "block" }} />
                      </div>
                      <div style={{ color: "white", fontSize: 10, fontWeight: 700, whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden", maxWidth: 76 }}>
                        {u?.nickname || formData.nickname || "訪客"}
                      </div>
                    </div>
                  ))}
              </div>
            </>
          )}
        </div> {/* <-- 正確關閉 舞台 (1280x720) */}
      </div>{/* end blur container */}

      {/* modal（置中，會在最上層顯示） */}
      {showProfile && (
        <ProfileModal
          user={profileUser}
          onClose={() => {
            setShowProfile(false);
            setProfileUser(undefined);
          }}
        />
      )}

      {/* 右上角 LOG IN 按鈕 */}
      {!loggedIn && (
        <button
          onClick={() => setShowLogin(true)}
          style={{
            position: "absolute",
            top: "calc(0% + 2%)",
            right: "40px",
            background: "rgba(0,0,0,0.6)",
            color: "white",
            padding: "0.3vw 0.5vw",
            borderRadius: "5px",
            border: "1px solid white",
            cursor: "pointer",
            fontWeight: "bold",
            fontSize: "0.9vw",
            zIndex: 9999,
          }}
        >
          LOG IN
        </button>
      )}
      {/* 登入視窗（點 LOG IN 才出現） */}
      {showLogin && (
        <div
          style={{
            position: "fixed",
            inset: 0,
            background: "rgba(0,0,0,0.35)",
            zIndex: 9999,
          }}
          onClick={() => setShowLogin(false)}
        >
          <div
            onClick={(e) => e.stopPropagation()}
            style={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              width: "434px",
              height: "302px",
              backgroundImage: "url('/login_bg.png')",
              backgroundSize: "100% 100%",
              backgroundRepeat: "no-repeat",
              position: "relative",
            }}
          >
            {/* X 圖片按鈕 */}
            <img
              src="/close.png"
              alt="Close"
              onClick={() => setShowLogin(false)}
              style={{
                position: "absolute",
                top: "15px",
                right: "30px",
                width: "36px",
                height: "32px",
                cursor: "pointer",
                zIndex: 2,
              }}
            />
            {/* 職業選擇 */}
            <div
            style={{
              position: "absolute",
              top: "83px", // 調整上下
              left: "53%",
              transform: "translateX(-50%)",
              width: "300px", // 覆蓋職業選擇區域的寬度
              height: "40px", // 加上固定高度，方便置中
              display: "flex",
              alignItems: "center", // 垂直置中
              justifyContent: "center", // 水平置中
              color: "black",
              fontSize: "15px",
              fontWeight: "bold",
              textAlign: "center",
             }}
            >
              {/* 左箭頭透明按鈕 */}
              <button
                onClick={handlePrevJob}
                style={{
                  position: "absolute",
                  left: "82px",   // 對齊背景綠色箭頭
                  top: "18px",
                  width: "30px", // 覆蓋箭頭大小
                  height: "30px",
                  background: "transparent",
                  //border: "2px solid red",
                  cursor: "pointer",
                }}
                aria-label="Prev"
              />

              {/* 中間職業名稱 */}
              <span>{professions[jobIndex]}</span>

              {/* 右箭頭透明按鈕 */}
              <button
                onClick={handleNextJob}
                style={{
                  position: "absolute",
                  right: "48px",  // 對齊背景綠色箭頭
                  top: "18px",
                  width: "30px",
                  height: "30px",
                  background: "transparent",
                  //border: "2px solid red",
                  cursor: "pointer",
                }}
                aria-label="Next"
              />
            </div>

            {/* 透明輸入欄 */}
            <input
              type="text"
              value={formData.department}
              onChange={(e) =>
                setFormData({ ...formData, department: e.target.value })
              }
              placeholder=""
              autoComplete="off"
              style={{
                position: "absolute",
                top: "125px",
                left: "26px",
                width: "305px",
                height: "28px",
                background: "transparent",
                border: "none",
                outline: "none",
                color: "#ffffffff",
                fontSize: 14,
                textAlign: "center",
              }}
            />
            <input
              type="text"
              value={formData.name}
              onChange={(e) =>
                setFormData({ ...formData, name: e.target.value })
              }
              placeholder=""
              autoComplete="off"
              style={{
                position: "absolute",
                top: "154px",
                left: "26px",
                width: "305px",
                height: "28px",
                background: "transparent",
                border: "none",
                outline: "none",
                color: "#ffffffff",
                fontSize: 14,
                textAlign: "center",
              }}
            />
            <input
              type="text"
              value={formData.nickname}
              onChange={(e) =>
                setFormData({ ...formData, nickname: e.target.value })
              }
              placeholder=""
              autoComplete="off"
              style={{
                position: "absolute",
                top: "183px",
                left: "20px",
                width: "305px",
                height: "28px",
                background: "transparent",
                border: "none",
                outline: "none",
                color: "#ffffffff",
                fontSize: 14,
                textAlign: "center",
              }}
            />
            {/* 透明 OK 按鈕 */}
            <button
              onClick={handleLogin}
              style={{
                position: "absolute",
                left: "165px",
                top: "252px",
                width: "108px",
                height: "38px",
                background: "transparent",
                border: "none",
                cursor: "pointer",
              }}
              aria-label="OK"
            />
          </div>
        </div>
      )}
      {/* 登出按鈕 */}
      {loggedIn && (
        <>
          <button
            onClick={() => {
              // 登出：清除使用者狀態並關閉可能開啟的 modal / login
              setLoggedIn(false);
              setShowProfile(false);
              setProfileUser(undefined);
              setCurrentUser({ nickname: "", avatar: "", loggedIn: false });
              setFormData({ department: "", name: "", nickname: "", avatar: "/avatar1.png" });
              setShowLogin(false);
            }}
            style={{
              position: "absolute",
              top: "20px",
              right: "40px",
              background: "rgba(0,0,0,0.6)",
              color: "white",
              padding: "0.3vw 0.6vw",
              borderRadius: "6px",
              border: "1px solid rgba(255,255,255,0.12)",
              cursor: "pointer",
              fontWeight: "700",
              border: "1px solid white",
              fontSize: "0.9vw",
              zIndex: 9999,
            }}
          >
            Log out
          </button>

          {/* 使用者顯示區：固定在舞台內，會跟視窗縮放（以右上角為錨點，位置不跑） */}
          <div
            style={{
              position: "absolute",
              top: 64,                    // 固定在視窗（或 stage）位置
              right: 40,
              width: 125,
              maxHeight: 160,
              overflowY: "auto",
              padding: "6px 8px",
              background: "rgba(0,0,0,0.45)",
              borderRadius: 8,
              display: "flex",
              flexDirection: "column",
              gap: 8,
              zIndex: 9999,
              boxSizing: "border-box",
              transform: `scale(${scale})`,        // 跟著目前 scale 一起放大/縮小
              transformOrigin: "top right",        // 以右上角為錨點，位置不會跑掉
              willChange: "transform",
              pointerEvents: "auto",
            }}
          >
             {/* 若未來有多位使用者可改成 map(profileList => ...)，目前顯示目前登入者 */}
             {[currentUser]
               .filter((u) => !!u && (u.loggedIn ?? loggedIn))
               .map((u, i) => (
                 <div key={i} style={{ display: "flex", alignItems: "center", gap: 8 }}>
                   <div style={{ width: 28, height: 28, borderRadius: "50%", overflow: "hidden", background: "#333", flexShrink: 0 }}>
                     <img src={u?.avatar || formData.avatar} alt="avatar" style={{ width: "100%", height: "100%", objectFit: "cover", display: "block" }} />
                   </div>
                   <div style={{ color: "white", fontSize: 10, fontWeight: 700, whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden", maxWidth: 76 }}>
                     {u?.nickname || formData.nickname || "訪客"}
                   </div>
                 </div>
               ))}
           </div>
        </>
      )}
    </main>
  );
}

